@charset "UTF-8";
/*——————————————
Global
———————————————*/
body {
  font-family: helvetica; }

/*——————————————
TimeLine CSS
———————————————*/
/* Base */
h2 {
  color: white; }

#content {
  margin-top: 50px;
  text-align: center; }

section.timeline-outer {
  width: 80%;
  margin: 0 auto; }

h1.header {
  font-size: 50px;
  line-height: 70px;
  color: white; }

/* Timeline */
.timeline {
  border-left: 8px solid #42a5f5;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  color: whitesmoke;
  margin: 50px auto;
  letter-spacing: 0.5px;
  position: relative;
  line-height: 1.4em;
  padding: 20px;
  list-style: none;
  text-align: left; }

.timeline h1,
.timeline h2,
.timeline h3 {
  font-size: 1.4em;
  color: white; }

.timeline .event {
  border-bottom: 1px solid rgba(160, 160, 160, 0.2);
  padding-bottom: 15px;
  margin-bottom: 20px;
  position: relative;
  color: white; }

.timeline .event:last-of-type {
  padding-bottom: 0;
  margin-bottom: 0;
  border: none; }

.timeline .event:before,
.timeline .event:after {
  position: absolute;
  display: block;
  top: 0; }

.timeline .event:before {
  left: -177.5px;
  color: white;
  content: attr(data-date);
  text-align: right;
  /*  font-weight: 100;*/
  font-size: 16px;
  min-width: 120px; }

.timeline .event:after {
  box-shadow: 0 0 0 8px #42a5f5;
  left: -30px;
  background: #212121;
  border-radius: 50%;
  height: 11px;
  width: 11px;
  content: '';
  top: 5px; }

/**/
/*——————————————
Responsive Stuff
———————————————*/
@media (max-width: 945px) {
  .timeline .event::before {
    left: 0.5px;
    top: 20px;
    min-width: 0;
    font-size: 13px; }
  .timeline h3 {
    font-size: 16px;
    color: white; }
  .timeline p {
    padding-top: 20px;
    color: white; }
  section.lab h3.card-title {
    padding: 5px;
    font-size: 16px;
    color: white; } }

@media (max-width: 768px) {
  .timeline .event::before {
    left: 0.5px;
    top: 20px;
    min-width: 0;
    font-size: 13px; }
  .timeline .event:nth-child(1)::before,
  .timeline .event:nth-child(3)::before,
  .timeline .event:nth-child(5)::before {
    top: 38px; }
  .timeline h3 {
    font-size: 16px;
    color: white; }
  .timeline p {
    padding-top: 20px;
    color: white; } }

/*——————————————
others
———————————————*/
a.portfolio-link {
  margin: 0 auto;
  display: block;
  text-align: center; }
